// optidrive/src/pages/FileExplorer.js
import React from 'react';
import { useRouter } from 'next/router'; // Import useRouter from next/router
import './FileExplorer.css';

const FileExplorer = ({ files = [] }) => { // Default to an empty array if files is undefined
    const router = useRouter(); // Use useRouter instead of useNavigate

    const handleFileClick = (file) => {
        if (file.isDirectory) {
            // Use router.push to navigate to the directory
            router.push(`/file-explorer/${file.name}`);
        } else {
            // Open the file (or handle file logic)
            console.log('Opening file:', file.name);
        }
    };

    return (
        <div className="file-explorer">
            <h1>File Explorer</h1>
            <div className="file-list">
                {files.map((file) => (
                    <div
                        key={file.name}
                        className={`file-item ${file.isDirectory ? 'folder' : 'file'}`}
                        onClick={() => handleFileClick(file)}
                    >
                        {file.isDirectory ? 'üìÅ' : 'üìÑ'} {file.name}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default FileExplorer;